/* global document, Office, Word */

Office.onReady((info) => {
  if (info.host === Office.HostType.Word) {
    document.getElementById("sideload-msg").style.display = "none";
    document.getElementById("app-body").style.display = "flex";
    document.getElementById("run").onclick = run;
  }
});




export async function run() {
  return Word.run(async (context) => {
    /**
     * Insert your Word code here
     */
    
    // get the selected range
    const range = context.document.getSelection();
    context.load(range, "text");
    await context.sync()
    
    const selectedText = range.text;
    console.log(selectedText);
    // call GPT API to generate a joke
    const newText = await makeMoreConcise(selectedText);

    // insert the joke
    range.insertText(newText, Word.InsertLocation.replace);

    await context.sync();
  });
}






// function to call GPT API to generate a joke
async function makeMoreConcise(selectedText) {
    const { Configuration, OpenAIApi } = require("openai");
  //require('dotenv-webpack').config()

  const configuration = new Configuration({
    apiKey: process.env.openaiApiKey,
  });

  const openai = new OpenAIApi(configuration);
  const completion = await openai.createCompletion({
      model: "text-davinci-003",
      prompt: "A more concise way of saying " + selectedText + " is: ",
    });
  const joke = completion.data.choices[0].text;

  return joke;
}
















